<template>
    <el-form  :model="form" label-width="80px" label-position="left">


        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-input v-model="form.id"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label=":" label-width="100px">
                    <el-select v-model="form.type" placeholder="">
                        <el-option v-for="(items,index) in select1" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>      

        <el-row>
            <el-col :span="4">
                <el-form-item label="">
                    <el-select v-model="form.state" placeholder="">
                        <el-option v-for="(items,index) in select2" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-form-item label="" v-model="form.time">
                    <el-date-picker type="date" placeholder=""></el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="4" :offset="1">
                <el-button type="primary" size="mini" @click="onSubmit"></el-button>
                <el-button size="mini" ></el-button>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="20">
                <el-table :data="searchData" border size="mini">
                    <el-table-column type="index" align="center" width="50px">
                    </el-table-column>
                     <el-table-column prop="mcode" label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column label="" align="center" width="100px"> 
                    </el-table-column>
                    <el-table-column label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column label="" align="center" width="120px">
                    </el-table-column>
                    <el-table-column label="" align="center" width="160px">
                    </el-table-column>
                    <el-table-column label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column label="" align="center" width="100px">
                    </el-table-column>
                    <el-table-column label="" align="center">
                    </el-table-column>
                    <el-table-column label="" align="center">
                    </el-table-column>
                    <el-table-column label="" align="center" width="100px">
                        <template slot-scope="scope">
                            <el-button type="text" size="small"></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="10">
                <el-pagination
                    :page-size="10"
                    layout="total, prev, pager, next"
                    :total="100">
                </el-pagination>    
            </el-col>
        </el-row>


    </el-form>
</template>


<script>
export default {
    data() {
        return {
            VisibleImg: "",
            select1:['','','','','','',''],
            select2:['','','','',''],
            form: {
                id: "", //
                name: "", //
                type:"", //
                state:"", //
                time: "" //
            },
            //
            searchData: []
        };
    },
    methods: {
        //
        onSubmit() {   
            this.$axios({
                method:'post',
                url:"/apis/member/findEditById",
                params:{
                    rId:this.form.id,
                }
            })     
            .then(response=>{
                //
                if(response.data.data){
                    this.searchData = response.data.data;
                }else {

                }
            })
        },
    }
};
</script>

<style>
</style>



