<template>
    <div class="wrap">

        <!--  -->
        <el-dialog :visible.sync="searchUser" width="90%" center>
            <el-form label-width="70px" label-position="left">
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input size="mini" v-model="searchFrom.mCode"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label="">
                            <el-input size="mini" v-model="searchFrom.mName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="3">
                        <el-form-item>
                            <el-button type="primary" size="mini" @click="onSearchDialog"></el-button>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="6">
                        <el-form-item label="">
                            <el-input size="mini" v-model="searchFrom.mobile"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="6" :offset="1">
                        <el-form-item label="">
                            <el-input size="mini" v-model="searchFrom.mNickname"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <el-table 
                ref="multipleTable" 
                :data="tableData" 
                tooltip-effect="dark" 
                border
                v-loading="loadingTable" 
                element-loading-text=""
                element-loading-spinner="el-icon-loading">
                <el-table-column label="" type="" width="55" align="center">
                    <template slot-scope="scope">
                        <el-radio class="radio" v-model="selectNum" :label="scope.$index" @change.native="getCurrentRow(scope.$index)">&nbsp;</el-radio>
                    </template>
                </el-table-column>
                <el-table-column prop="mCode" label="" width="80" align="center" sortable>                   
                </el-table-column>
                <el-table-column prop="mName" label="" width="90" align="center">
                </el-table-column>
                <el-table-column prop="mNickname" label="" align="center">
                </el-table-column>
                 <el-table-column prop="mId" label="" align="center" width="100">
                </el-table-column>
                 <el-table-column prop="" label="" align="center" width="100">
                </el-table-column>
                 <el-table-column prop="mobile" label="" align="center" width="100">
                </el-table-column>
                 <el-table-column label="" width="50" align="center">
                     <template slot-scope="scope">
                         {{scope.row.gender==0?'':''}}
                    </template>
                </el-table-column>
                 <el-table-column prop="birthdate" label="" align="center" width="90">
                </el-table-column> 
                <el-table-column prop="creationData" label="" align="center" width="90">
                </el-table-column>
                <el-table-column prop="updateDate" label="" align="center" width="90">
                </el-table-column>
                <el-table-column prop="mStatus" label="" align="center" width="50">
                </el-table-column>
                <el-table-column prop="mLevel" label="" align="center" width="80">
                </el-table-column>
                <el-table-column prop="province" label="" align="center">
                </el-table-column>
                <el-table-column prop="city" label="" align="center">
                </el-table-column>
                <el-table-column prop="country" label="" align="center">
                </el-table-column>
                <el-table-column prop="detial" label="" align="center" width="200">
                </el-table-column>
                <el-table-column prop="addPost" label="" width="70" align="center">
                </el-table-column>
            </el-table>
            <br>
            <el-row type="flex" justify="center">
                <el-col :span="8" >
                    <el-pagination
                        :page-size="searchFrom.pageSize"
                        layout="total, sizes, prev, pager, next"
                        :page-sizes="[2, 3, 4, 5]"
                        :total="searchFrom.total"
                        :current-page="searchFrom.currentPage"
                        @current-change="onChangePage"
                        @size-change="handleSizeChange">
                    </el-pagination>
                </el-col>
            </el-row>
            <span slot="footer" class="dialog-footer">
                <el-button size="mini" @click="searchUser = false"> </el-button>
                <el-button type="primary" size="mini" @click="sendData"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="DialogVisible" width="300px" center>
            <p class="text-center"></p>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="DialogVisible = false"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="DialogBank" width="400px" center>
            <el-form :rules="rulesBank" :model="fromBank" ref="fromBank" label-width="70px" label-position="left">
                <el-form-item label="" prop="type">
                    <el-select v-model="fromBank.type" placeholder="" >
                        <el-option v-for="(items,index) in fromBank.select" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
                <br>
                <el-form-item label="" prop="name">
                    <el-input v-model="fromBank.name"></el-input>
                </el-form-item>
                <br>
                <el-form-item label="" prop="number">
                    <el-input v-model="fromBank.number"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="DialogBank = false"> </el-button>
                <el-button type="primary" size="mini" @click="onAddBank('fromBank')"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title=""  :visible.sync="DialogAddress" width="600px" center>
            <el-form status-icon :rules="rulesAddress" :model="fromAddress" ref="fromAddress" label-width="80px" label-position="left">
                <el-form-item label="" class="inline-block" prop="address">
                    <div class="area" >
                        <area-select type="text" :level="2" :placeholders="placeholders" v-model="fromAddress.address"  :data="pcaa"></area-select>
                    </div>
                </el-form-item>
                <br><br>
                <el-form-item label="" prop="detial">
                    <el-input v-model="fromAddress.detial"></el-input>
                </el-form-item>
                <br>
                <el-form-item label="" prop="name">
                    <el-input v-model="fromAddress.name"></el-input>
                </el-form-item>
                <br>
                <el-form-item label="" prop="tel">
                    <el-input v-model="fromAddress.tel"></el-input>
                </el-form-item>
                <br>
                <el-form-item label="">
                    <el-input v-model="fromAddress.phone"></el-input>
                </el-form-item>
                <br>
                <el-form-item label="" label-width="120px">
                    <el-radio-group v-model="fromAddress.defaultAdd">
                        <el-radio label="" ></el-radio>
                        <el-radio label=""></el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="DialogAddress = false"> </el-button>
                <el-button type="primary" size="mini" v-if="BtnAddAddress" @click="onSendAddress('fromAddress')"></el-button>
                <el-button type="primary" size="mini" v-if="BtnChangeAddress" @click="OnchangeAddress('fromAddress')"></el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="DialogRemoveAddress" width="300px" center>
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="DialogRemoveAddress = false"></el-button>
                <el-button type="primary" size="mini" @click="onRemoveAddress"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="DialogUntieBank" width="300px" center>
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="DialogUntieBank = false"></el-button>
                <el-button type="primary" size="mini" @click="onUntieBank"> </el-button>
            </span>
        </el-dialog>

        

        <!--  -->
        <el-dialog title="" :visible.sync="onClose" width="300px" center>
            <p class="text-center">{{title}}</p>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="onClose = false"> </el-button>
            </span>
        </el-dialog>
    </div>

</template>


<script>
import Vue from 'vue'
import util from "../../util/util.js";
import { pca, pcaa } from "area-data";
export default {
    name: "dialog-com",
    data() {
        //
        var validateTel = (rule, value, callback) => {
            const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
            let isTel = reg.test(value);
            if ( !isTel) {
                callback(new Error(''));
            } else {
                callback();
            }
        };
        return {
            placeholders: ["", "", ""],
            pca: pca,
            pcaa,
            showHead:true,  //
            loadingTable:true, //
            selectNum:"",   //
            BtnAddAddress:false,  //
            BtnChangeAddress:false,  //
            DialogVisible: false,  //
            DialogRemoveAddress: false,  //
            DialogUntieBank:false,  //
            DialogAddress: false,  //
            onClose: false,  //
            title:"",   //
            searchUser: false,  //
            DialogBank: false,  //
            tempData:null,
            tableData: [],  //
            //
            fromBank:{
                type:"",
                name:"",
                number:"",
                select: ['','','','','','','','','','','','','','','']
            },
            //
            fromAddress: {
                address:[],
                detial:"",
                name:"",
                tel:"",
                phone:"",
                defaultAdd:""
            },
            //
            searchFrom:{
                currentPage:1,
                pageSize:5,
                total:null,
                mCode:"",
                mName:"",
                mobile:"",
                mNickname:""
            },
            //
            rulesAddress: {
                address: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                detial: [
                    { required: true, message: "", trigger:['blur','change'] },
                    { min: 1, max: 50, message: " 1  50 ", trigger: ['blur','change'] }
                ],
                name: [
                    { required: true, message: "", trigger: ['blur','change'] },
                    { min: 1, max: 30, message: " 1  30 ", trigger: ['blur','change'] }
                    ], 
                tel: [
                    { required: true, message: "", trigger: ['blur','change'] },
                    { validator: validateTel, trigger: ['blur','change']}    
                ]
            },
            //
            rulesBank: {
                type: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                name: [
                    { required: true, message: "", trigger: ['blur','change'] },
                    { min: 2, max: 8, message: " 2  8 ", trigger: ['blur','change'] }
                ],
                number: [
                    { required: true, message: "", trigger: ['blur','change'] },
                    { min: 16, max: 19, message: "1619", trigger: ['blur','change'] }
                ]
            }
            
        };
    },
    methods: {       
        //
        onSearchDialog() {
            this.loadingTable = true; 
            this.getMemberinfo();
        },
        //
        onChangePage(currentPage) {
            this.searchFrom.currentPage = currentPage;
            this.getMemberinfo();
        },
        //
        handleSizeChange(pageSize) {
            this.searchFrom.pageSize = pageSize;
            this.getMemberinfo();
        },
        //
        getMemberinfo() {
            this.$axios({
                method:'post',
                url:"/apis/member/search",
                params:{
                    currentPage:this.searchFrom.currentPage,
                    pageSize:this.searchFrom.pageSize,
                    mCode:this.searchFrom.mCode,
                    mName:this.searchFrom.mName,
                    mobile:this.searchFrom.mobile,
                    mNickname:this.searchFrom.mNickname
                }
            })     
            .then(response=>{
                this.tableData = response.data.data.list;
                this.searchFrom.total = response.data.data.total;
                for(let i = 0; i < response.data.data.list.length; i++){
                    this.$axios({
                        method:'get',
                        url:"/apis/member/findRelationByMCode",
                        params:{
                            mCode:response.data.data.list[i].mCode,
                        }
                    })     
                    .then(response=>{ 
                        if(response.data.code){
                            this.tableData[i].mStatus = response.data.data.mStatus;
                            this.tableData[i].mLevel = response.data.data.rank;
                            Vue.set(this.tableData,i,this.tableData[i])
                        }
                    })
                    
                }
                this.loadingTable = false;
            })
        },
        //,
        getCurrentRow(val) {
            this.selectNum =val;
        },
        //
        sendData() {
            //
            if(this.selectNum===""){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }else {
                util.$emit("TabelData",this.tableData[this.selectNum]);
                this.searchUser = false;
            }
        },


        //
        onSendAddress(fromAddress) {
            this.$refs[fromAddress].validate((valid) => {
                if (valid) {
                    util.$emit("Addaddress",this.fromAddress)
                    this.DialogAddress = false;
                } else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                    return false;
                }
            });
        },
        //
        OnchangeAddress(fromAddress) {
            this.$refs[fromAddress].validate((valid) => {
                if (valid) {
                    this.$axios({
                        method:'post',
                        url:"/apis/member/editMemAdd",
                        params: {
                            aId:this.fromAddress.aId,
                            mCode:this.fromAddress.mCode,
                            consigneeName:this.fromAddress.name,
                            mobile:this.fromAddress.tel,
                            phone:this.fromAddress.phone,
                            addProvinceCode:this.fromAddress.address[0],
                            addCityCode:this.fromAddress.address[1],
                            addCountryCode:this.fromAddress.address[2],
                            addDetial:this.fromAddress.detial,
                            addPost:this.fromAddress.addPost,
                            defaultAdd:this.fromAddress.defaultAdd==""?1:0
                        }
                    })
                    .then(response=>{
                        if(response.data.code){
                            this.$message({
                                message: '',
                                type: 'success'
                            });
                            util.$emit("addressChangeSuccess");
                            this.DialogAddress = false;
                        } else{
                            console.log("");
                        }
                    })   

                } else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                    return false;
                }
            });
        },
        //
        onRemoveAddress() {
            this.$axios({
                method:'get',
                url:"/apis/member/delMemAddByAId",
                params: {
                    aId:this.fromAddress.aId,
                    mCode:this.fromAddress.mCode
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        message: '!',
                        type: 'success'
                    });
                    util.$emit("RemoveAddressSuccess");
                    this.DialogRemoveAddress = false;
                } else{
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
            })    
        },


        //
        onAddBank(fromBank) {
            this.$refs[fromBank].validate((valid) => {
                if (valid) {
                    this.$axios({
                        method:'post',
                        url:"/apis/member/addBankByMCode",
                        params: {
                            mCode:this.fromBank.mid,
                            bankCode:this.fromBank.type,
                            accName:this.fromBank.name,
                            accCode:this.fromBank.number
                        }
                    })
                    .then(response=>{
                        if(response.data.code){
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'success'
                            });
                        this.DialogBank = false;
                        util.$emit("addBankSuccess");
                        } else{
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'error'
                            });
                        }
                    }) 
                } else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                    return false;
                }
            });
              
        },
        //
        onUntieBank() {
            console.log(this.tempData.aId)
            this.$axios({
                method:'get',
                url:"/apis/member/delBankByOid",
                params: {
                    oId:this.tempData.aId,
                    mCode:this.tempData.mCode
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    });
                    util.$emit("UntieBankSuccess")
                    this.DialogUntieBank = false;
                } else{
                    console.log("");
                }
            }) 
        }

    },
    created (){
        //
        util.$on("showdialog",()=>{
            this.DialogVisible = true;
        });

        //
        util.$on("userDefined",(val)=>{
            this.onClose = true;
            this.title = val;
        })


        //
        util.$on("DialogAddAddress",()=>{
            this.fromAddress = {
                address:[],
                detial:"",
                name:"",
                tel:"",
                phone:"",
                defaultAdd:""
            },
            this.DialogAddress = true;
            this.BtnChangeAddress = false;
            this.BtnAddAddress = true;
        })
        //
        util.$on("DialogAddressRemove",(data)=>{
            this.DialogRemoveAddress = true;
            this.fromAddress.aId = data.aId,
            this.fromAddress.mCode = data.mCode
        })
        //
        util.$on("DialogAddressChange",(data)=>{
            this.BtnAddAddress = false;
            this.BtnChangeAddress = true;
            this.$axios({
                method:'get',
                url:"/apis/member/findMemAddByMCode",
                params: {
                    aId:data.aId,
                    mCode:data.mCode
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.fromAddress.aId = response.data.data.aId,
                    this.fromAddress.mCode = response.data.data.mCode,
                    this.fromAddress.addPost = response.data.data.addPost,
                    this.fromAddress.address[0] = response.data.data.addProvinceCode,
                    this.fromAddress.address[1] = response.data.data.addCityCode,
                    this.fromAddress.address[2] = response.data.data.addCountryCode,
                    this.fromAddress.detial = response.data.data.addDetial,
                    this.fromAddress.name = response.data.data.consigneeName,
                    this.fromAddress.tel = response.data.data.mobile,
                    this.fromAddress.phone = response.data.data.phone,
                    this.fromAddress.defaultAdd = response.data.data.defaultAdd==1?'':'',
                    this.DialogAddress = true;
                } else{
                    console.log("");
                }
            })    
        })


        //
        util.$on("searchdialog",(val)=>{
            if(val) {
                this.searchFrom.mCode = val;
            }
            this.searchUser = true;
            this.selectNum = "";
            this.getMemberinfo();
        });


        //
        util.$on("DialogBank",(mid)=>{
            this.fromBank.type = "",
            this.fromBank.name = "",
            this.fromBank.number = "",
            this.DialogBank = true;
            this.fromBank.mid = mid;
        });
        //
        util.$on("dialogUntieBank",(data)=>{
            this.tempData = data;
            this.DialogUntieBank = true;
        });
    } 
};
</script>

<style>
.el-dialog__wrapper .el-form-item {
    margin-bottom: 0px;
}
.el-radio > .el-radio__label { 
    display: none;
}
</style>



