<template>
    <div class="wrap">
        <!--  -->
        <div class="line">
            <span></span>
            <el-form ref="form" :model="formSearch" label-width="40px" label-position="left">
                <el-row>
                    <el-col :span="5">
                        <el-col :span="12">
                            <el-form-item label="" label-width="70px">
                                <el-select v-model="formSearch.id" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11" :offset="1">
                            <el-input v-model="formSearch.inputId"></el-input>
                        </el-col>
                    </el-col>

                    <el-col :span="5" :offset="1">
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-select v-model="formSearch.nickname" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11" :offset="1">
                            <el-input v-model="formSearch.inputNickName"></el-input>
                        </el-col>
                    </el-col>

                    <el-col :span="5" :offset="1">
                        <el-col :span="12">
                            <el-form-item label="">
                                <el-select v-model="formSearch.name" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11" :offset="1">
                            <el-input v-model="formSearch.inputName"></el-input>
                        </el-col>
                    </el-col>

                    <el-col :span="5" :offset="1">
                        <el-col :span="12">
                            <el-form-item label="" label-width="70px">
                                <el-select v-model="formSearch.tel" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11" :offset="1">
                            <el-input v-model="formSearch.inputTel"></el-input>
                        </el-col>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="5">
                        <el-col :span="12">
                            <el-form-item label="" label-width="70px">
                                <el-select v-model="formSearch.grId" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11" :offset="1">
                            <el-input v-model="formSearch.inputGrId"></el-input>
                        </el-col>
                    </el-col>
                    <el-col :span="5" :offset="1">
                        <el-col :span="12">
                            <el-form-item label="" label-width="70px">
                                <el-select v-model="formSearch.grname" placeholder="">
                                    <el-option v-for="(items,index) in selectData1" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="11" :offset="1">
                            <el-input v-model="formSearch.inputGrName"></el-input>
                        </el-col>
                    </el-col>
                    <el-col :span="2" :offset="1">
                        <el-form-item label="">
                            <el-select v-model="formSearch.ostatus" placeholder="">
                                <el-option v-for="(items,index) in selectData2" :key="index" :label="items" :value="items"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="9" :offset="1">
                        <el-col :span="9">
                            <el-form-item label="" label-width="70px">
                                <el-date-picker type="date" placeholder="" v-model="formSearch.joioTime" style="width:auto;"></el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="" label-width="50px" class="text-center">
                                <el-select v-model="formSearch.levelFrom" placeholder="">
                                    <el-option v-for="(items,index) in selectData3" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                        <el-col :span="7">
                            <el-form-item label="" label-width="30px" class="text-center">
                                <el-select v-model="formSearch.levelTo" placeholder="">
                                    <el-option v-for="(items,index) in selectData3" :key="index" :label="items" :value="items"></el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-col>
                </el-row>
            </el-form>
        </div>
        <!-- ||  -->
        <el-row>
            <el-col :span="8">
                <div class="block">
                    <el-pagination
                        :page-size="10"
                        layout="total, prev, pager, next"
                        :total="100">
                    </el-pagination>
                </div>
            </el-col>
            <el-col :span="12"> 
                <el-col :span="3"><el-button size="small" type="primary" @click="onSearch"></el-button></el-col>
                <el-col :span="3"><el-button size="small" ></el-button></el-col>
                <el-col :span="3"><el-button size="small" type="success" @click="exportExcel"></el-button></el-col>       
            </el-col>
        </el-row>
        <br/>

        <!--  -->
        <el-table 
        ref="memberTable" 
        :data="searchData" 
        border 
        tooltip-effect="dark" 
        id="memberTable" 
        size="mini"
        v-loading="loadingTable" 
        element-loading-text=""
        element-loading-spinner="el-icon-loading">
            <el-table-column label="" align="center" width="50px" >
                <template slot-scope="scope">
                    <el-radio class="radio" v-model="radio" :label="scope.$index" @change.native="getCurrentRow(scope.$index)">&nbsp;</el-radio>
                </template>
            </el-table-column>
            <el-table-column prop="mCode" label="" align="center" width="70px">
            </el-table-column>
            <el-table-column prop="mName" label="" align="center" width="80px">
            </el-table-column>
            <el-table-column prop="mNickname" label="" align="center" width="80px">
            </el-table-column>
            <el-table-column prop="mId" label="" align="center" width="100px">
            </el-table-column>
            <el-table-column prop="" label="" align="center" width="100px">
            </el-table-column>
            <el-table-column prop="mobile" label="" align="center" width="100px">
            </el-table-column>
            <el-table-column label="" width="50" align="center">
                    <template slot-scope="scope">
                        {{scope.row.gender==0?'':''}}
                </template>
            </el-table-column>
                <el-table-column prop="birthdate" label="" align="center">
            </el-table-column> 
            <el-table-column prop="creationData" label="" align="center">
            </el-table-column>
            <el-table-column prop="updateDate" label="" align="center">
            </el-table-column>
            <el-table-column prop="mStatus" label="" align="center" width="50">
            </el-table-column>
            <el-table-column prop="mLevel" label="" align="center" width="80">
            </el-table-column>
            <el-table-column prop="province" label="" align="center">
            </el-table-column>
            <el-table-column prop="city" label="" align="center">
            </el-table-column>
            <el-table-column prop="country" label="" align="center">
            </el-table-column>
            <el-table-column prop="detial" label="" align="center" width="200">
            </el-table-column>
            <el-table-column prop="addPost" label="" width="70" align="center">
            </el-table-column>
        </el-table>

         <!--  -->
        <dialog-com></dialog-com>
    </div>
</template>


<script>
import util from "../../util/util.js";
import FileSaver from "file-saver";
import XLSX from "xlsx";
export default {
    data() {
        return {
            loadingTable:true, //
            //
            selectData1: [
                "...",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            selectData2: ["", "", "", ""],
            selectData3: [
                "",
                "",
                "vip",
                "",
                "",
                "",
                "",
                "",
                "",
                ""
            ],
            radio: "",
            //
            formSearch: {
                id: "", //
                nickname: "", //
                name: "", //
                tel: "", //
                grId: "", //
                grname: "", //
                ostatus: "", //
                joioTime: "", //
                levelFrom: "", //from
                levelTo: "", //to
                inputId: "", //
                inputNickName: "", //
                inputName: "", //
                inputTel: "", //
                inputGrId: "", //
                inputGrName: "" //
            },
            currentPage: 1, //
            //
            searchData: []
        };
    },
    methods: {
        //
        onSearch() {
            //
            let term = this.formSearch;
            if(term.inputId!=""||term.inputNickName!=""||term.inputName!=""||term.inputTel!=""||term.inputGrId!=""||term.inputGrName!=""){
                //
                this.$axios.get("/apis/member/findByMCode", {
                    params:{
                        mCode:this.formSearch.inputId,
                    }
                })
                .then(response=>{
                    if(!response.data.data){
                        //
                        util.$emit("userDefined","")
                    }else {
                        this.searchData.push(response.data.data);
                    }
                })
            } else{
                //
                console.log("")
                util.$emit("userDefined","")
                
            }
            
        },
        //
        handleSizeChange() {},
        //
        handleCurrentChange() {},
        //
        exportExcel() {
            var wb = XLSX.utils.table_to_book(
                document.querySelector("#memberTable")
            );
            var wbout = XLSX.write(wb, {
                bookType: "xlsx",
                bookSST: true,
                type: "array"
            });
            try {
                FileSaver.saveAs(
                    new Blob([wbout], { type: "application/octet-stream" }),
                    ".xlsx"
                );
            } catch (e) {
                if (typeof console !== "undefined") console.log(e, wbout);
            }
            return wbout;
        },
        //
        getMemberinfo() {   
            this.$axios({
                method:'post',
                url:"/apis/member/search",
                params:{
                    mCode:this.formSearch.id,
                    mName:this.formSearch.name,
                    mobile:this.formSearch.tel,
                    mNickname:this.formSearch.nickname
                }
            })     
            .then(response=>{
                this.searchData = response.data.data;
                this.loadingTable = false;
            })
        }
    },
    created () {
        this.getMemberinfo();
    }
};
</script>

<style>
.line {
    width: 100%;
    position: relative;
    display: inline-block;
    border: 1px solid #888;
    border-radius: 10px;
    padding: 20px;
    margin-bottom: 30px;
}

.line > span {
    position: absolute;
    top: -10px;
    padding: 0 10px;
    background: #fff;
}
.line .el-input__inner {
    margin-top: 7px;
}
.serch-input .el-form-item__content {
    position: relative;
}
.serch-input .el-icon-search {
    position: absolute;
    right: 10px;
    top: 12px;
    font-size: 18px;
    color: #666;
    cursor: pointer;
}
.el-icon-search:hover {
    color: #409eff;
}
.el-form--inline .el-form-item {
    margin-bottom: 0;
}
.area-select.medium {
    width: 90px;
}
.text-center .el-form-item__label {
    text-align: center;
}
.text-center .long-input {
    min-width: 400px;
}
.number-count {
    padding: 0 10px;
}
.btn-center .el-form-item__content {
    display: flex;
    justify-content: space-around;
}
</style>



