<template>
    <el-form ref="form" status-icon :rules="rules" :model="form" label-width="80px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" size="mini" @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>
       
        <el-row>
            <el-col :span="6">
                <el-form-item label="" prop="tel">
                    <el-input v-model="form.tel" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="" prop="nickname">
                    <el-input v-model="form.nickname" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <template>
            <el-form-item label="" label-width="120px" @change="changeForm">
                <el-radio v-model="form.withdrawDefault" label=""></el-radio>
            </el-form-item>
        </template>

        <el-row>
            <el-col :span="14">
                <el-form-item label="" label-width="100px">
                    <el-button type="text" @click="addBank"></el-button>
                    <el-table :data="allBankTable" border size="mini">
                        <el-table-column prop="bankCode" label="" align="center" width="150px">
                        </el-table-column>
                        <el-table-column prop="accCode" label="" align="center"> 
                        </el-table-column>
                        <el-table-column prop="accName" label="" align="center" width="150px">
                        </el-table-column>
                        <el-table-column prop="accType" label="" align="center" width="100px">
                        </el-table-column>
                        <el-table-column label="" align="center" width="80px">
                            <template slot-scope="scope">
                                <el-button type="text" size="small" @click="untieBank(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-col>
        </el-row>


        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="desc">
                    <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" size="mini"  @click="onSubmit('form')" :loading="submitLoading"></el-button>
        </el-form-item>

        <dialog-com></dialog-com>
    </el-form>
</template>


<script>
import util from "../../../util/util.js";
export default {
  data() {
    //
    var validateTel = (rule, value, callback) => {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        let isTel = reg.test(value);
        if ( !isTel) {
            callback(new Error(''));
        } else {
            callback();
        }
    };
    return {
        isChangeFrom:false,  //
        bankTable:[],       //
        submitLoading:false,  //loading
        form: {
            id: "", //
            name: "", //
            nickname:"", //
            tel: "", //
            withdrawDefault: "", //
            desc: ""//
        },
        //
        rules: {
            nickname: [
                { required: true, message: "", trigger: "blur" },
                { min: 1, max: 5, message: " 1  5 ", trigger: "blur" }
            ],
            tel: [
                { required: true, message: "", trigger: "blur" },
                { validator: validateTel, trigger: 'blur'}
            ] 
        },
        //
        allBankTable: []
    };
  },
  methods: {
    // 
    changeForm() {
        this.isChangeFrom = true;
    },
    //
    onSubmit(form) {  
        if(!this.form.id) {    //
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });       
        }else if(!this.isChangeFrom){
            this.$message({
                showClose: true,
                message: '!',
                type: 'error'
            }); 
        }else{
            this.$refs[form].validate((valid) => {
                if (valid) {
                    this.submitLoading = true;
                    this.$axios({
                        method:'post',
                        url:"/apis/member/updateByMBank",
                        params: {
                            mCode:this.form.id,
                            mName:this.form.name,
                            mobile:this.form.tel,
                            mNickname:this.form.nickname,
                            withdrawDefault:this.form.withdrawDefault,
                            mDesc:this.form.desc
                        }
                    })
                    .then(response=>{
                        if(response.data.code){
                            util.$emit("userDefined","")
                            this.isChangeFrom = false;
                            this.submitLoading = false;
                        } else{
                            util.$emit("userDefined","")
                        }
                    }) 
                } else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    }); 
                    return false;
                }
            });

             
            
        }
    },
    //
    onSearch() {
      util.$emit("searchdialog");
    },
    //
    getBankList() {
        this.$axios({
            method:'get',
            url:"/apis/member/findMBankByMCode",
            params: {
                mCode:this.form.id
            }
        })
        .then(response=>{
            if(response.data.code){
                this.allBankTable = [];
                let leng = response.data.data.memberBank.length;
                let data = response.data.data.memberBank;
                for(var i = 0; i < leng; i++){
                    let obj = data[i];
                    this.allBankTable.push(obj);
                }
            } else{
                console.log("");
            }
        })          
        
    },
    //
    untieBank(row) {
        util.$emit("dialogUntieBank",{
            aId:row.oId,
            mCode:row.mCode
        });
    },
    //
    addBank() {
        if(this.form.id){
            util.$emit("DialogBank",this.form.id);
        }else {
            this.$message({
                showClose: true,
                message: '',
                type: 'error'
            });
        }
    }
  },
  created () {
    //
    util.$on("TabelData",(data)=>{
        this.form.id = data.mCode;
        this.form.name = data.mName;
        this.form.tel = data.mobile;
        this.form.nickname = data.mNickname;
        this.getBankList();
        setTimeout(()=>{
            this.isChangeFrom = false;
        },500)
    });
    //
    util.$on("addBankSuccess",()=>{
        this.getBankList();
        this.isChangeFrom = true;
    });
    //
     util.$on("UntieBankSuccess",()=>{
         this.getBankList();
         this.isChangeFrom = true;
     })
  }
};
</script>

<style>
.address .el-form-item__label {
  text-align: center;
}
</style>



