<template>
    <el-form ref="form" :rules="rules" :model="form" label-width="110px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" size="mini" @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.nickname" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.joinData" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.memberStatu" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.memberLevel" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.refereeId" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.refereeName" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label=":" class="serch-input" prop="newRefereeId">
                    <el-input @change="changeForm" v-model="form.newRefereeId" placeholder=""></el-input>
                    <i class="el-icon-search" @click="onSearchInput"></i>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="" prop="newRefereeName">
                    <el-input @change="changeForm" v-model="form.newRefereeName"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="remarks">
                    <el-input @change="changeForm" type="textarea" v-model="form.remarks" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" size="mini" :loading="submitLoading" @click="onSubmit('form')"></el-button>
        </el-form-item>

        <dialog-com></dialog-com>
    </el-form>
</template>


<script>
import util from "../../../util/util.js";
export default {
    data() {
        return {
            isChangeFrom:false,  //
            submitLoading:false,  //loading
            isInputSearch:false,    //
            form: {
                id: "", //
                name: "", //
                nickname: "", //
                joinData:"",  //
                memberStatu:"",   //
                memberLevel:"", //
                refereeId:"",  //
                refereeName:"",  //
                newRefereeId:"",  //
                newRefereeName:"",  //
                remarks:"",  //
            },
            //
            rules: {
                newRefereeId: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                newRefereeName: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                remarks: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ]
            }
        };
    },
    methods: {
        // 
        changeForm() {
            this.isChangeFrom = true;
        },
        //
        onSubmit(form) {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else if(!this.isChangeFrom){
                this.$message({
                    showClose: true,
                    message: '!',
                    type: 'error'
                }); 
            }else{
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/updateRelaSponByMCode",
                            params: {
                                mCode:this.form.id,
                                mName:this.form.name,
                                sponsorCode:this.form.refereeId, 
                                sponsorName:this.form.refereeName,
                                sponsorCodeNew:this.form.newRefereeId,
                                sponsorNameNew:this.form.newRefereeName,
                                mDesc:this.form.remarks
                            }
                        })
                        .then(response=>{
                            console.log(response)
                            if(response.data.code){
                                util.$emit("userDefined",",!");
                                this.submitLoading = false;
                                this.isChangeFrom = false;
                            } else{
                                util.$emit("userDefined","!")
                                this.submitLoading = false;
                            }
                        })   
                    } else {
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        });
                        return false;
                    }
                }); 
            }
        },
        //
        onSearch() {
            this.isInputSearch = false;
            util.$emit("searchdialog");  
            this.showArea = false;  
            setTimeout(()=>{
                this.showArea = true;  
            },200)
        },
        //
        onSearchInput() {
            this.isInputSearch = true;
            util.$emit("searchdialog",this.form.newRefereeId);  
            this.showArea = false;  
            setTimeout(()=>{
                this.showArea = true;  
            },200)
        },
        //
        getMemberInfo() {
            this.$axios({
                method:'get',
                url:"/apis/member/findRelationByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.form.memberStatu = response.data.data.mStatus;
                    this.form.memberLevel = response.data.data.rank;
                    this.form.refereeId = response.data.data.sponsorCode;
                    this.form.refereeName = response.data.data.sponsorName;
                    this.isChangeFrom = false;
                } else{
                }
            })    
        },
    },
    created() {
        //
        util.$on("TabelData",(data)=>{
            if(this.isInputSearch) {
                this.form.newRefereeId = data.mCode;
                this.form.newRefereeName = data.mName;
            }else {
                this.form.id = data.mCode;
                this.form.name = data.mName;
                this.form.nickname = data.mNickname;
                this.form.joinData = data.creationData;
                this.getMemberInfo();
                setTimeout(()=>{
                    this.isChangeFrom = false;
                },500)
            }
        });
    }
};
</script>

<style>
i.el-icon-plus{
    font-size: 32px;
    color: #c3c3c3;
}
.el-upload-dragger {
    width: 34px;
    height: 34px;
    line-height: 34px;
}
.el-upload--picture-card {
    width: 34px;
    height: 34px;
    line-height: 34px;
    border: 1px solid #ccc;
}
.el-upload-list--picture-card .el-upload-list__item.is-success {
    width:36px;
    height: 36px;
}
.el-upload-list--picture-card .el-upload-list__item-status-label {
    right: -19px;
    top: -10px;
}
.el-upload-list--picture-card .el-upload-list__item-status-label i {
    transform: scale(0.8) rotate(-45deg);
}
.el-upload__tip {
    line-height: 20px;
}
.serch-input .el-form-item__content {
    position: relative;
}
.serch-input .el-icon-search {
    position: absolute;
    right: 10px;
    top: 12px;
    font-size: 18px;
    color: #666;
    cursor: pointer;
}
.el-icon-search:hover {
    color: #409eff;
}
</style>



