<template>
    <el-form ref="form" :rules="rules"  :model="form" label-width="100px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" size="mini" @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.nickname" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.joinData" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.memberStatu" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.memberLevel" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.mobile" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.arrearsBalance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.accountState" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.bonusPointsBalance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.shoppingBalance" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.replacementBalance" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.changeName" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>        
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-select v-model="form.IDType" placeholder="" @change="changeForm">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                        <el-option label="" value=""></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.IDNumber" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="20">
                <el-form-item label="">
                    <el-upload action="https://jsonplaceholder.typicode.com/posts/"
                      list-type="picture-card"
                      :file-list="form.proofMaterial" 
                      @change="changeForm">
                        <div slot="tip" class="el-upload__tip">png/JPG/bmp5M</div>
                        <i class="el-icon-plus"></i>                       
                    </el-upload>
                    <el-dialog >
                        <img width="100%" alt="">
                    </el-dialog>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.changeNickname" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.changeMobile" @change="changeForm"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="16">
                <el-form-item label="">
                    <el-button type="text" @click="addBank"></el-button>
                    <el-button type="text" @click="onDialogRemoveBank" v-show="showRemoveBank"></el-button>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="12">
                <el-form-item label="">
                    <el-table  border size="mini" :data="allBankTable">
                        <el-table-column prop="bankCode" label="" align="center" width="160px">
                        </el-table-column>
                        <el-table-column prop="accCode" label="" align="center"> 
                        </el-table-column>
                        <el-table-column prop="accName" label="" align="center" width="100px">
                        </el-table-column>
                        <el-table-column prop="accType" label="" align="center" width="80px">
                        </el-table-column>
                    </el-table>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="16">
                <el-form-item label="">
                    <el-button type="text" @click="onDialogAddress"></el-button>
                    <el-button type="text" @click="onDialogRemoveAddress" v-show="showRemoveAddress"></el-button>
                </el-form-item>
            </el-col>
        </el-row>
        <el-row>
            <el-col :span="24">
                <el-form-item label="" class="inline-block" prop="addAddress">
                    <div class="area">
                        <area-select type="text" @change="changeForm" :level="2" :placeholders="placeholders" v-model="form.addAddress" :data="pcaa"></area-select>
                    </div>
                </el-form-item>
                <el-form-item  label="" label-width="100px" class="text-center inline-block" prop="detial">
                    <el-input class="long-input" v-model="form.detial"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="8">
                <el-form-item label="" prop="remarks">
                    <el-input @change="changeForm" v-model="form.remarks" type="textarea" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" size="mini" @click="onSubmit('form')" :loading="submitLoading"></el-button>
        </el-form-item>

        <dialog-com></dialog-com>


        <!--  -->
        <el-dialog title="" :visible.sync="dialogAddressList" width="550px" center>
            <el-table  border size="mini" :data="allAddressTable">
                <el-table-column label="" align="center" type="index" width="60">
                </el-table-column>
                <el-table-column prop="address" label="" align="center"> 
                </el-table-column>
            </el-table>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogRemoveAddress" width="300px" center>
            <p class="text-center"></p>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="dialogRemoveAddress = false"> </el-button>
                <el-button type="primary" size="mini" @click="onRemoveAddress"> </el-button>
            </span>
        </el-dialog>
        <!--  -->
        <el-dialog title="" :visible.sync="dialogRemoveBank" width="300px" center>
            <p class="text-center"></p>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size="mini" @click="dialogRemoveBank = false"> </el-button>
                <el-button type="primary" size="mini" @click="onRemoveBank"> </el-button>
            </span>
        </el-dialog>

    </el-form>
</template>


<script>
import util from "../../../util/util.js";
import { pca, pcaa } from "area-data";
export default {
    data() {
        return {
            isChangeFrom:false,  //
            submitLoading:false,  //loading
            dialogBankList: false,  //
            dialogRemoveBank: false, //
            dialogAddressList:false,  //
            dialogRemoveAddress:false,  //
            showRemoveBank: false, //
            showRemoveAddress: false, //
            showArea:true,
            placeholders: ["", "", ""],
            pca: pca,
            pcaa,
            allBankTable: [],  //
            allAddressTable: [],   //
            //
            newBankTable:[
                {
                    bankName:"",
                    bankId:"",
                    name:"",
                    type:""
                }
            ],
            form: {
                id: "", //
                name: "", //
                nickname: "", //
                joinData:"",  //
                memberStatu:"",   //
                memberLevel:"", //
                mobile:"",  //
                arrearsBalance:"",  //
                accountState:"",    //
                bonusPointsBalance:"",  //
                shoppingBalance:"",  //
                replacementBalance:"",  //
                changeName:"",  //
                IDType:"",  //
                IDNumber:"",  //
                proofMaterial:[ //
                    {
                        name: 'food.jpeg',
                        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
                    {
                        name: 'food2.jpeg', 
                        url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'
                    }
                ],
                changeNickname:"",  //
                changeMobile:"",  //
                addAddress:[],  //
                detial:"", //
                remarks:"",  //

            },
            //
            rules: {
                addAddress: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                detial: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ],
                remarks: [
                    { required: true, message: "", trigger: ['blur','change'] },
                ]
            }
        };
    },
    methods: {
        // 
        changeForm() {
            this.isChangeFrom = true;
        },
        //
        onSubmit(form) {
            if(!this.form.id) {     //
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });       
            }else if(!this.isChangeFrom){
                this.$message({
                    showClose: true,
                    message: '!',
                    type: 'error'
                }); 
            }else{
                this.$refs[form].validate((valid) => {
                    if (valid) {
                        //
                        switch(this.form.IDType){
                            case "": 
                                this.form.IDType = 1 ;
                                break;
                            case "": 
                                this.form.IDType = 2 ;
                                break;
                            case "": 
                                this.form.IDType = 3 ;
                                break;
                            case "": 
                                this.form.IDType = 4 ;
                                break;
                            case "": 
                                this.form.IDType = 5 ;
                                break;
                        }
                        this.submitLoading = true;
                        this.$axios({
                            method:'post',
                            url:"/apis/member/updateIdByMCodeAndMName",
                            params: {
                                mCode:this.form.id,
                                mName:this.form.name,
                                newMName:this.form.nickname,
                                idType: this.form.IDType,
                                idCode: this.form.IDNumber,
                                mNickname: this.form.nickname,
                                mobile: this.form.mobile,
                                province: this.form.addAddress[0],
                                city: this.form.addAddress[1],
                                country: this.form.addAddress[2],
                                detial: this.form.detial,
                                mDesc:this.form.remarks
                            }
                        })
                        .then(response=>{
                            if(response.data.code){
                                util.$emit("userDefined",",!");
                                this.submitLoading = false;
                                this.isChangeFrom = false;
                            } else{
                                util.$emit("userDefined","!")
                                this.submitLoading = false;
                            }
                        })   
                    } else {
                        this.$message({
                            showClose: true,
                            message: '!',
                            type: 'error'
                        });
                        return false;
                    }
                }); 
            }
        },
        //
        onSearch() {
            util.$emit("searchdialog");  
            this.showArea = false;  
            setTimeout(()=>{
                this.showArea = true;  
            },200)
        },
        //
        getMemberInfo() {
            this.form.arrearsBalance = "";
            this.form.accountState = "";
            this.form.bonusPointsBalance = "";
            this.form.shoppingBalance = "";
            this.form.replacementBalance = "";
            this.$axios({
                method:'get',
                url:"/apis/member/findMemAccountByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.form.arrearsBalance = response.data.data.bonusBlance;
                    this.form.accountState = response.data.data.bonusStatus;
                    this.form.bonusPointsBalance = response.data.data.bonusBlance;
                    this.form.shoppingBalance = response.data.data.bonusBlance;
                    this.form.replacementBalance = response.data.data.redemptionBlance;
                    this.isChangeFrom = false;
                } else{
                    util.$emit("userDefined","!")
                }
            })    
        },
        //
        getBankList() {
            this.$axios({
                method:'get',
                url:"/apis/member/findMBankByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.data.memberBank.length != 0){
                    this.showRemoveBank = true; 
                    this.allBankTable = response.data.data.memberBank;
                }else {
                    this.showRemoveBank = false; 
                }
            }) 
        },
        //
        onDialogRemoveBank() {
            if(this.form.id){
                this.dialogRemoveBank = true;
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        onRemoveBank() {
            this.$axios({
                method:'get',
                url:"/apis/member/outAllBankByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    });
                    this.allBankTable = [];
                    this.showRemoveBank = false; 
                    this.isChangeFrom = true;
                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
                this.dialogRemoveBank = false;
            }) 
        },
        //
        addBank() {
            if(this.form.id){
                util.$emit("DialogBank",this.form.id);
                this.isChangeFrom = true;
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },

        //
        onDialogAddress() {
            if(this.form.id){
                this.dialogAddressList = true;
                this.getAddressList();
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        getAddressList() {
            this.$axios({
                method:'get',
                url:"/apis/member/findAddAllByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.data.length!=0){
                    this.showRemoveAddress = true;
                    this.allAddressTable = [];
                    for(var i = 0; i < response.data.data.length; i++){
                        let obj = {
                            address:response.data.data[i].addProvinceCode+"-"+response.data.data[i].addCityCode+"-"+response.data.data[i].addCountryCode+"-"+response.data.data[i].addDetial
                        }
                        this.allAddressTable.push(obj);
                    }
                } else{
                    //
                    this.allAddressTable = [];
                    this.showRemoveAddress = false;
                }
            })  
        },
        //
        onDialogRemoveAddress() {
            if(this.form.id){
                this.dialogRemoveAddress = true;
            }else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
        },
        //
        onRemoveAddress() {
            this.$axios({
                method:'get',
                url:"/apis/member/outAllAddByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'success'
                    });
                    this.dialogRemoveAddress = false;
                    this.showRemoveAddress = false;
                    this.isChangeFrom = true;
                }else {
                    this.$message({
                        showClose: true,
                        message: '!',
                        type: 'error'
                    });
                }
                this.DialogRemoveBank = false;
            }) 
        }
    },
    created() {   
        //
        util.$on("TabelData",(data)=>{
            this.form.id = data.mCode;
            this.form.name = data.mName;
            this.form.nickname = data.mNickname;
            this.form.joinData = data.creationData;
            this.form.memberStatu = data.idType;
            this.form.memberLevel = data.idType;
            this.form.mobile = data.mobile;
            this.getBankList();
            this.getAddressList();
            setTimeout(()=>{
                this.isChangeFrom = false;
            },500)
        });
        //
        util.$on("addBankSuccess",()=>{
            this.getBankList();
            this.isChangeFrom = true;
        });
    }
};
</script>

<style>
i.el-icon-plus{
    font-size: 32px;
    color: #c3c3c3;
}
.el-upload-dragger {
    width: 34px;
    height: 34px;
    line-height: 34px;
}
.el-upload--picture-card {
    width: 34px;
    height: 34px;
    line-height: 34px;
    border: 1px solid #ccc;
}
.el-upload-list--picture-card .el-upload-list__item.is-success {
    width:36px;
    height: 36px;
}
.el-upload-list--picture-card .el-upload-list__item-status-label {
    right: -19px;
    top: -10px;
}
.el-upload-list--picture-card .el-upload-list__item-status-label i {
    transform: scale(0.8) rotate(-45deg);
}
.el-upload__tip {
    line-height: 20px;
}
.area-select.medium {
    width: 100px;
    margin-top: 7px;
}
.area-select-wrap .area-selected-trigger{
  line-height: 28px;
  text-align: center;
  padding: 0;
}
.area-select:hover{
    border-color: #409EFF;
}
</style>



