<template>
    <el-form ref="form" :model="form" label-width="110px" label-position="left">

        <el-form-item label="">
            <el-button type="primary" size="mini" @click="onSearch"></el-button>
        </el-form-item>

        <el-row>
            <el-col :span="6">
                <el-form-item label="">
                    <el-input v-model="form.id" disabled></el-input>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-input v-model="form.name" disabled></el-input>
                </el-form-item>
            </el-col>
        </el-row>      

        <el-row>
            <el-col :span="6">
                <el-form-item label=":">
                    <el-select v-model="form.currentType" placeholder="">
                        <el-option v-for="(items,index) in select" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
            <el-col :span="6" :offset="1">
                <el-form-item label="">
                    <el-select v-model="form.nextType" placeholder="">
                        <el-option v-for="(items,index) in select" :key="index" :label="items" :value="items"></el-option>
                    </el-select>
                </el-form-item>
            </el-col>
        </el-row>

        <el-row>
            <el-col :span="8">
                <el-form-item label="">
                    <el-input type="textarea" v-model="form.desc" :autosize="{ minRows: 4, maxRows: 6}"></el-input>
                </el-form-item>
            </el-col>
        </el-row>

        <el-form-item>
            <el-button type="primary" size="mini" @click="onSubmit"></el-button>
        </el-form-item>

        <dialog-com></dialog-com>
    </el-form>
</template>


<script>
import util from "../../../util/util.js";
export default {
    data() {
        return {
            isChangeFrom:false,  //
            submitLoading:false,  //loading
            select:['...','','VIP','','','','','','',''],
            form: {
                id: "33434", //
                name: "", //
                currentType:"", //
                nextType:"", //
                desc: "" //
            }
        };
    },
    methods: {
        //
        onSubmit() {
            util.$emit("showdialog");
        },
        //
        onSearch() {
            util.$emit("searchdialog");
        },
        //
        getMemberInfo() {
            this.$axios({
                method:'get',
                url:"/apis/member/findRelationByMCode",
                params: {
                    mCode:this.form.id
                }
            })
            .then(response=>{
                if(response.data.code){
                    this.form.currentType = response.data.data.rank;
                    this.isChangeFrom = false;
                } else{
                }
            })    
        },
    },
    created() {
        //
        util.$on("TabelData",(data)=>{
            this.form.id = data.mCode;
            this.form.name = data.mName;
            this.getMemberInfo();
            setTimeout(()=>{
                this.isChangeFrom = false;
            },500)
        });
    }
};
</script>

<style>
</style>



